<script>
export default {
  data() {
    return {
      fiscalYear: '',
      daysLeft: 0
    }
  },
  onLoad() {
    this.updateCountdown();
  },
  methods: {
    updateCountdown() {
      const now = new Date();
      // ËÆ°ÁÆóÂΩìÂâçË¥¢ÊîøÂπ¥Â∫¶Ôºà7Êúà1Êó•ÂºÄÂßãÔºåÊ¨°Âπ¥6Êúà30Êó•ÁªìÊùüÔºâ
      const year = now.getMonth() + 1 >= 7 ? now.getFullYear() : now.getFullYear() - 1;
      const endDate = new Date(year + 1, 5, 30, 23, 59, 59); // Ê¨°Âπ¥6Êúà30Êó•
      const startTwoDigits = String(year).slice(2);
      const endTwoDigits = String(year + 1).slice(2);
      this.fiscalYear = `${startTwoDigits}-${endTwoDigits}`;
      const diff = Math.max(0, Math.ceil((endDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24)));
      this.daysLeft = diff;
    },
    onCapsuleTap() {
      uni.showActionSheet({
        itemList: ['ÂèçÈ¶à', 'ÂàÜ‰∫´'],
        success: (res) => {
          if (res.tapIndex === 0) uni.showToast({ title: 'ÊÑüË∞¢ÂèçÈ¶à', icon: 'none' })
          if (res.tapIndex === 1) uni.showToast({ title: 'Â∑≤ÂàÜ‰∫´', icon: 'success' })
        }
      })
    },
    goIndex() {
      uni.reLaunch({ url: '/pages/index/index' });
    },
    goTrends() {
